import {combineReducers} from 'redux';
import * as ActionTypes from './actionTypes';
import localStorage from '../util/storage';

/*
 reducerstateactionstate
 */

//
const initialState = {
    showStatus: false, //
    song: localStorage.getCurrentSong(), //localStorage
    songs: localStorage.getSongs(), //localStorage
    skin: localStorage.getSkin()  //
}

//reducer
//
function showStatus(showStatus = initialState.showStatus, action) {
    switch (action.type) {
        case ActionTypes.SHOW_PLAYER:
            return action.showStatus;
        default:
            return showStatus;
    }
}

//
function song(song = initialState.song, action){
    switch (action.type){
        case ActionTypes.CHANGE_SONG:
            localStorage.setCurrentSong(action.song); //localStorage
            return action.song;
        default:
            return song;
    }
}

//
function songs(songs = initialState.songs, action){
    switch(action.type){
        case ActionTypes.SET_SONGS:
            localStorage.setSongs(action.songs);
            console.log(action.songs)
            return action.songs;
        case ActionTypes.REMOVE_SONG_FROM_LIST:
            let newSongs = songs.filter(song => song.id !== action.id);
            localStorage.setSongs(newSongs);
            return newSongs;
        default:
            return songs;

    }
}

//
function skin(skin = initialState.skin, action){
    switch (action.type) {
        case ActionTypes.SET_SKIN:
            localStorage.setSkin(action.skin);
            return action.skin;
        default:
            return skin;
    }
}

//reducercombineReducersreducerreducerstatekey
const reducer = combineReducers({
    showStatus,
    song,
    songs,
    skin
})

export default reducer;


